.trending-bg-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width:100vw;
    height:100vh;
}

.trending-container-header{

}

.trending-container-sidebar-videos{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    /* padding:10px; */
    overflow: auto;
    scrollbar-width:none ;
    height: 91%;
    width: 100%;
    /* border:solid red; */
    /* margin-top: 20px; */
    /* margin-top:80px; */
    
}

.trending-container-sidebar{
    /* position: fixed; */
    /* border:solid blue; */
    /* margin-right: 10px; */
    width: 22%;
    min-height: 100%;
}

.trending-container{
    display: flex;
    min-height: 100%;
    width: 78%;
    flex-direction: column;
    /* justify-content: flex-end; */
    /* border:solid green; */
}

.trending-subs-container{
    display: flex;
    align-items: center;
    /* margin-bottom: 15px; */
    padding: 10px;
}
.trending-subs{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 110%;
    width: 8%;
    margin-left: 30px;
    /* background-color: black; */
    border-radius: 50%;
}

.trending-subs-logo{
    width: 40%;
    height: 50% ;
    /* background-color: black; */
    color: red;
}

.trending-subs-heading{
    height: 60%;
    margin-bottom: 8px;
    margin-left: 10px;
}

.trending-videos-cointainer{
    background-color:#f4f4f4 ;
    /* overflow-y: scroll; */
    scrollbar-width:none ;
    /* border:solid pink; */
}

.trending-videos{
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: space-between;
    margin: 10px;
}

.trending-each-video{
    display: flex;
    justify-content: center;
    margin: 30px;
    /* margin-bottom: 100px; */
    width: 93.8%;
}

.trending-each-video-thumb{
    width: 40%;
    height: 35%;
    cursor: pointer;
}

.trending-title-channelname{
    padding: 5px;
    margin-left: 10px;
    width: 100%;
}

.trending-title{
    margin-top: 0px;
    margin-bottom: 0px;
}

.trending-channel-name{
    margin-top: 10px;
    margin-bottom:0px ;
    color:#64748b;
}

.trending-count-publish{
    display: flex;
    /* justify-content: space-between; */
    margin-top: 5px;
    color:#64748b;
}

.trending-view-count{
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: 10px;
}

.trending-publish{
    margin-top: 0px;
    /* margin-left: 10px; */
    margin-bottom: 0px;
    font-size: small;
    /* font-weight: bold; */
}

.trending-loading{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (min-width:320px) and (max-width:425px){
    .trending-container-sidebar-videos{
        padding: 10px;
    }
    .trending-container-sidebar{
        display: none;
    }
    .trending-container{
        min-width:100%;
    }
    .trending-each-video{
        flex-direction: column;
    }
    .trending-each-video-thumb{
        min-width:100%;
    }
    .trending-subs{
        height: 90%;
        width: 18%;
    }
    .trending-subs-logo{
        width: 50%;
    }
}