.header-cointainer{
    display:flex;
    width: 100%;
    height: 9%;
    justify-content: space-between;
    align-items: center;
    /* background-color: white; */
    position:fixed;
    /* margin-left: 0px; */
    /* border:solid green; */
}

.header-logo-cointainer{
    display: flex;
    /* justify-content: flex-start; */
    height: 30px;
    width:15%;
    /* border: solid blue; */
    margin-left: 50px;
    margin-bottom: 0px;
}

.website-logo{
    width: 100%;
    height: 100%;
    /* margin-left: 60px; */
    /* margin-top: 18px; */
    /* margin-bottom: 0px; */
    cursor: pointer;
    /* border:solid red; */
}

.header-buttons-cointainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* margin: 5px; */
    /* padding-top: 15px; */
    width: 20%;
    height: 100%;
    /* border: solid yellow; */
}

.header-theme-container{
    /* display:flex; */
    /* border: solid red; */
    width: 13%;
    height: 30px;
    /* margin-right:30px ; */
}

.theme-dark{
    width: 100%;
    height: 100%;
    /* background-color: white; */
    /* border: solid blue; */
    cursor: pointer;
    /* color:black; */
}
.theme-light{
    /* visibility: hidden; */
    cursor: pointer;
    width: 100%;
    height: 100%;
    color:white;
}

.header-profile-container{
    /* border: solid green; */
    width: 15%;
    height: 30px;
    /* margin-right:30px ; */
}

.profile-logo{
    width: 100%;
    height: 100%;
    /* border: solid purple; */
    cursor: pointer;
}

.profile-icon{
    visibility: hidden;
    cursor: pointer;
    width: 100%;
    height: 100%;
}

.header-logout-container{
    /* align-items: center; */
    /* border: solid orange; */
    width: 35%;
    padding-top: 0px;
    /* padding-left: 10px; */
    padding-right: 10px;
    height: 20px;
}

.logout-button{
    
    /* padding: 5px; */
    /* background-color: white; */
    /* color: #3b82f6; */
    /* border: 2px solid #3b82f6; */
    /* padding-left: 10px;
    padding-right: 10px; */
    width: 100%;
    height: 100%;
    cursor: pointer;
    /* border: solid pink; */
}

.logout-icon{
    visibility: collapse;
    cursor: pointer;
    width: 100%;
    height: 100%;
    /* background-color: white; */
    border: none;
}

@media(width:768px){
    .header-profile-container{
        height: 60%;
    }
    .logout-button{
        font-size: x-small;
        margin-top: 0px;
    }
}

@media (min-width:320px) and (max-width:425px){
    .header-theme-container{
        width: 40px;
        height: 17px;
    }
    .header-profile-container{
        width: 50px;
        height: 30px;
    }
    .header-logout-container{
        width: 60px;
        height: 50px;
    }
    .header-logo-cointainer{
        margin-left:10px;
    }
    .profile-logo{
        display:none;
    }
    .profile-icon{
        visibility: visible;
    }
    .logout-button{
        display: none;
    }
    .logout-icon{
        visibility: visible;
    }
}