.sidebar-bg-container{
    display: flex;
    flex-direction: column;
    justify-content:space-between;
    position:fixed;
    width:22%;
    height: 91%;
    /* border: solid palevioletred; */
}

.sidebar-container{
    display: flex;
    flex-direction: column;
    cursor: pointer;
}

.sidebar-home-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
    cursor: pointer;
    /* border: solid green; */
    height: 25%;
    max-height:25%;
}

.sidebar-home-container:active{
    background-color: #cbd5e1;
}

.sidebar-home-logo{
    /* margin-right: 20px; */
    margin-left: 10px;
    width: 25px;
    height: 25px;
    /* border: solid red; */
}
.sidebar-home-logo:active{
    color: red;
}

.sidebar-label{
    /* justify-content: flex-start; */
    font-size: large;
    cursor: pointer;
    /* border: solid blue; */
    width: 80%;
}

.sidebar-trending-cointainer{
    display:flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    max-height:25%;
    height: 25%;
    /* border: solid green; */
}
.sidebar-trending-cointainer:active{
    background-color: #cbd5e1;
}

.sidebar-trending-logo{
    /* margin-right: 20px; */
    margin-left: 10px;
    width: 25px;
    height: 25px;
    /* border: solid orange; */
}
.sidebar-home-logo:active{
    color: red;
}

.sidebar-gaming-container{
    display:flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    max-height:25%;
    height: 25%;
    /* border: solid green; */
}
.sidebar-gaming-container:active{
    background-color: #cbd5e1;
}

.sidebar-gaming-logo{
    /* margin-right: 22px; */
    margin-left: 10px;
    width: 25px;
    height: 25px;
    /* border: solid olive; */
}
.sidebar-home-logo:active{
    color: red;
}

.sidebar-saved-container{
    display:flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    max-height:25%;
    height: 25%;
    /* border: solid green; */
}
.sidebar-saved-container:active{
    background-color: #cbd5e1;
}
.sidebar-saved-container.sidebar-saved-logo:active{
    color:red;
}

.sidebar-saved-logo{
    /* margin-right: 21px; */
    margin-left: 10px;
    width: 25px;
    height: 25px;
    /* border: solid yellow; */
}
/* .sidebar-home-logo:active{
    color: red;
} */

.sidebar-contact-container{
    display: flex;
    flex-direction: column;
}

.sidebar-contact-heading{
    margin-left:10px;
}

.sidebar-contact-logos-container{
    display: flex;
    margin-left:10px;
}

.sidebar-contact-logos{
    width: 15%;
    height: 80%;
    margin-right: 10px;
    cursor: pointer;
}

.sidebar-contact-para{
    margin-left:10px;
    font-weight: normal;
}

@media(max-width:576px){
    .sidebar-bg-container{
        display: none;
    }
}