.home-bg-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width:100vw;
    height:100vh;
    /* border:solid blue; */
}

.home-container-header{
    /* width: 99.55%; */
    /* max-height: 20%; */
    /* position: fixed; */
    /* border:solid green; */
    /* margin-bottom:-10px; */
}

.home-container-sidebar-videos{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    /* padding:10px; */
    /* margin-top: -10px; */
    /* border: solid red; */
    height: 91%;
    width: 100%;
    /* position:fixed; */
}

.home-container-sidebar{
    /* margin-right: 10px; */
    width: 22%;
    min-height: 100%;
}

.home-container-videos{
    display: flex;
    min-height: 100%;
    width: 78%;
    flex-direction: column;
    /* justify-content: flex-end; */
    overflow-y: scroll;
    scrollbar-width:none ;
}

.home-subs-container{
    background-image: url('https://assets.ccbp.in/frontend/react-js/nxt-watch-banner-bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: space-between;
    /* margin-bottom: 10px; */
    /* padding: 10px; */
    /* border: solid red; */
}

.subs-container{
    display: flex;
    flex-direction: column;
    margin-left:20px;
}

.subs-logo{
    width: 30%;
    height: 40%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.subs-heading{
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: normal;
}

.subs-button{
    margin-top: 20px;
    margin-bottom: 20px;
    width: 30%;
    padding: 10px;
    border: solid black;
    font-weight:bold ;
    cursor: pointer;
}

.subs-close-container{

}

.subs-close-button{
    cursor: pointer;
}

.home-search-videos-cointainer{
    background-color:#f4f4f4 ;
    /* border: solid green; */
}

.search-container{
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    margin-left:30px;
    justify-content: flex-start;
    width:40%;
    height: 30px;
    border: none;
    margin-bottom: 10px;
    /* border: solid yellow; */
}

.search-bar{ 
    padding: 8px;
    width:92%;
    max-height: 100%;
    border: 1px solid grey;
}

.search-button{
    width: 8%;
    height: 100%;
    cursor:pointer;
    align-self: center;
    border: 1px solid grey;
}

.home-videos{
    display: flex;
    justify-content: center;
    flex-wrap:wrap;
    /* margin: 10px; */
    /* height: 100%; */
    /* border: solid blue; */
}

.home-each-video{
    margin: 10px;
    margin-bottom: 100px;
    width: 30%;
    height: 5%;
}

.home-each-video-thumb{
    width: 100%;
    height: 35%;
    cursor: pointer;
}

.home-video-main{
    display: flex;
    justify-content: space-between;
}

.home-channel-logo{
    width: 30%;
    min-height: 10%;
}

.channel-logo{
    width: 100%;
    height: 30%;
    padding-top: 5px;
}

.home-title-channelname{
    padding: 5px;
}

.home-title{
    margin-top: 0px;
    margin-bottom: 0px;
}

.home-channel-name{
    margin-top: 10px;
    margin-bottom:0px ;
    color:#64748b;
}

.home-count-publish{
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
    color:#64748b;
}

.home-view-count{
    margin-top: 0px;
    margin-bottom: 0px;
}

.home-publish{
    margin-top: 0px;
    margin-bottom: 0px;
    font-size: small;
    /* font-weight: bold; */
}

.no-search-container{
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    /* border: solid red; */
}

.no-search-image{
    width: 40%;
    height: 40%;
}

.no-search-heading{
    margin-bottom: 5px;
}

.no-search-para{
    /* margin-top: 0px; */
}

.no-search-button{
    background-color: #3b82f6;
    border: none;
    border-radius:5px;
    padding: 8px;
    padding-left: 20px;
    padding-right:20px;
    cursor: pointer;
}

.home-loading{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (width:768px){
    .search-button{
        width:11%;
        /* align-self: center; */
    }
    .home-videos{
        margin: 0px;
    }
    .home-each-video{
        
        margin: 5px;
        margin-bottom: 50px;
        /* border: solid red; */
    }
}

@media(min-width:320px) and (max-width:425px){
    .home-container-sidebar-videos{
        padding: 10px;
    }
    .home-container-sidebar{
        display: none;
    }
    .search-container{
        min-width:95%;
        height:0.15%;
    }
    .home-container-videos{
        width: 100%;
        padding: 5px;
        /* border: solid red; */
    }
    .home-each-video{
        display:flex;
        flex-direction:column;
        flex-wrap:nowrap;
        min-width: 100%;
        margin: 5px;
        margin-bottom: 20px;
    }
    .search-container{
        margin-left: 10px;
    }
}
