.gaming-bg-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width:100vw;
    height:100vh;
}

.gaming-container-header{

}

.gaming-container-sidebar-videos{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    scrollbar-width:none ;
    /* padding:10px; */
    height: 91%;
    width: 100%;
    overflow: auto;
    /* margin-top: 80px; */
}

.gaming-container-sidebar{
    /* position: fixed; */
    /* margin-right: 10px; */
    width: 22%;
    min-height: 100%;
}

.gaming-container{
    display: flex;
    min-height: 100%;
    width: 78%;
    flex-direction: column;
    /* justify-content: flex-end; */
}

.gaming-subs-container{
    display: flex;
    align-items: center;
    /* margin-bottom: 15px; */
    padding: 10px;
}
.gaming-subs{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 110%;
    width: 8%;
    margin-left: 30px;
    /* background-color: black; */
    border-radius: 50%;
}

.gaming-subs-logo{
    width: 40%;
    height: 50% ;
    /* background-color: black; */
    color: red;
}

.gaming-subs-heading{
    height: 60%;
    margin-bottom: 8px;
    margin-left: 10px;
}

.gaming-videos-cointainer{
    background-color:#f4f4f4 ;
    /* overflow-y: scroll; */
}

.gaming-videos{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 10px;
    /* padding-left: 25px; */
    padding-top: 20px;
}

.gaming-each-video{
    display: flex;
    flex-direction: column;
    margin: 10px;
    margin-bottom: 70px;
    width: 20%;
    height: 40%;
}

.gaming-each-video-thumb{
    width: 100%;
    height: 90%;
    cursor: pointer;
    border-radius: 10px;
}

.gaming-title{
    margin-top: 0px;
    margin-bottom: 0px;
}

.gaming-view-count{
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: 10px;
    color:#64748b;
}

.gaming-loading{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (min-width:320px) and (max-width:425px){
    .gaming-container-sidebar-videos{
        padding:10px;
    }
    .gaming-container-sidebar{
        display: none;
    }
    .gaming-container{
        min-width: 100%;
    }
    .gaming-videos{
        margin: 0px;
        width: 95%;
        padding-left: 0px;
        padding-top: 0px;
        /* border: solid red; */
    }
    .gaming-each-video{
        margin: 10px;
        margin-bottom: 30px;
        width: 40%;
        min-height: 60%;
        /* border: solid blue; */
    }
    .gaming-subs{
        height: 90%;
        width: 18%;
    }
    .gaming-subs-logo{
        width: 50%;
    }
}