.login-bg-container{
    max-width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-form-container{
    width: 350px;
    height: 300px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}

.form-bg-container{
    padding: 12px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-self: center;
    width: 50vw;
    max-height:80vh; 
    border: none;
} 

.login-website-logo{
    width: 150px;
    display: flex;
    align-self: center;
    padding: 10px;
}

.login-heading{
    font-size: 15px;
    color:grey;
}

.login-fields{
    margin-top: 5px;
    margin-bottom: 15px;
    padding: 5px;
}

.checkbox-container{
    display:flex;
    font-weight: bold;
}

.login-button{
    background-color: #3b82f6;
    margin-top: 10px;
    padding: 8px;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.login-error{
    color: red;
    margin-top: 0px;
    margin-bottom: 0px;
}
