.saved-bg-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width:100vw;
    height:100vh;
}

.saved-container-header{

}

.saved-container-sidebar-videos{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    /* padding:10px; */
    /* margin-top: 20px; */
    overflow: auto;
    scrollbar-width:none ;
    height: 91%;
    width: 100%;
    /* margin-top:80px; */
}

.saved-container-sidebar{
    /* position: fixed; */
    /* margin-right: 10px; */
    width: 22%;
    min-height: 100%;
}

.saved-container{
    display: flex;
    min-height: 100%;
    width: 78%;
    flex-direction: column;
}

.saved-subs-container{
    display: flex;
    align-items: center;
    /* margin-bottom: 15px; */
    padding: 10px;
}
.saved-subs{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 8%;
    margin-left: 30px;
    /* background-color: black; */
    border-radius: 50%;
}

.saved-subs-logo{
    width: 40%;
    height: 50% ;
    /* background-color: black; */
    color: red;
}

.saved-subs-heading{
    height: 60%;
    /* margin-top: 10px; */
    margin-left: 10px;
}

.saved-videos-cointainer{
    background-color:#f4f4f4 ;
    /* overflow-y: scroll; */
    /* scrollbar-width:none ; */
    /* border: solid blue; */
}

.saved-videos{
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: space-between;
    margin: 10px;
    /* border: solid green; */
}

.saved-each-video{
    display: flex;
    /* justify-content: center; */
    margin: 30px;
    /* margin-bottom: 100px; */
    width: 93.8%;
    /* border: solid yellow; */
}

.saved-thumb{
    width:60%;
    height:40%;
    /* border:solid red; */
}

.saved-each-video-thumb{
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.saved-title-channelname{
    padding: 5px;
    margin-left: 10px;
    width: 100%;
}

.saved-title{
    margin-top: 0px;
    margin-bottom: 0px;
}

.saved-channel-name{
    margin-top: 10px;
    margin-bottom:0px ;
    color:#64748b;
}

.saved-count-publish{
    display: flex;
    /* justify-content: space-between; */
    margin-top: 5px;
    color:#64748b;
}

.saved-view-count{
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: 10px;
}

.saved-publish{
    margin-top: 0px;
    /* margin-left: 10px; */
    margin-bottom: 0px;
    font-size: small;
    /* font-weight: bold; */
}

.no-savedvideos-container{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.no-savedvideos-image{
    width: 30%;
    height: 20%;
    margin-bottom: 0px;
}

.no-savedvideos-heading{
    margin-bottom: 0px;
}

.no-savedvideos-description{

}

@media (min-width:320px) and (max-width:425px){
    .saved-container-sidebar-videos{
        padding: 10px;
    }
    .saved-container-sidebar{
        display: none;
    }
    .saved-container{
        min-width:100%;
    }
    .saved-each-video{
        flex-direction: column;
    }
    .saved-each-video-thumb{
        min-width:100%;
    }
    .saved-subs{
        height: 90%;
        width: 18%;
    }
    .saved-subs-logo{
        width: 50%;
    }
    .no-savedvideos-image{
        width:10%;
        height: 10%;
    }
}