    margin-top: 0px;
    font-size: smaller;
    color:#64748b;
}

.detailedview-description{
    /* border: solid peru; */
    margin-left: 70px;
}

@media (width:768px){
    .detailedview-container-sidebar-videos{
        overflow: auto;
    }
    .detailedview-count-buttons-container{
        width: 83%;
    }
}

@media (min-width:320px) and (max-width:425){
    .detailedview-container-sidebar{
        display:none;
    }
    .detailedview-container{
        width:100%;
        padding:5px;
    }
    .detailedview-container-sidebar-videos{
        overflow: auto;
    }
}